import React, { useContext } from "react";
import AuthContext from "../context/AuthContext";
import Classes from './Cart.module.scss'
import Container from "./Container";

const Cart = () => {
  const ctx = useContext(AuthContext);
  const sushiPrice = parseInt(22.99 * ctx.getSushiVlaue);
  const schintzelPrice = parseInt(16.50 * ctx.getSchintzelValue);
  const burgerPrice = parseInt(12.99 * ctx.getBurgervalue);
  const greenPrice = parseInt(18.99 * ctx.getGreenValue);

  const totalPrice = (sushiPrice + schintzelPrice + burgerPrice + greenPrice).toFixed(2);
  return (
    <React.Fragment>
      {
        ctx.isFlag &&
        <div className={Classes.cart}>
          {
            ctx.menu.map((items =>
              < Container value={items.value} title={items.title} id={items.id} amount={items.amount} />
            ))

          }
          {/* {ctx.getSchintzelValue ? (<div className={Classes.cartDiv}>
            <div>
              <h1>
                Schintzel
              </h1>
              <div>
                <p>$16.99</p>
                <p>x{ctx.getSchintzelValue}</p>
              </div>
            </div>
            <div className={Classes.changeValue}>
              <button onClick={() => { ctx.minus('schintzel') }}>-</button><button onClick={() => { ctx.checkId('schintzel') }}>+</button>
            </div>
            <div className={Classes.underline}></div>
          </div>) : (<div style={{ display: "none" }}></div>)
          }
          {ctx.getBurgervalue ? (<div className={Classes.cartDiv}>
            <div>
              <h1>
                Burger
              </h1>
              <div>
                <p>$12.99</p>
                <p>x{ctx.getBurgervalue}</p>
              </div>
            </div>
            <div className={Classes.changeValue}>
              <button onClick={() => { ctx.minus('barbecue') }}>-</button><button onClick={() => { ctx.checkId('barbecue') }}>+</button>
            </div>
            <div className={Classes.underline}></div>
          </div>) : (<div style={{ display: "none" }}></div>)
          }
          {ctx.getGreenValue ? (<div className={Classes.cartDiv}>
            <div>
              <h1>
                Green Bowl
              </h1>
              <div>
                <p>$18.99</p>
                <p>x{ctx.getGreenValue}</p>
              </div>
            </div>
            <div className={Classes.changeValue}>
              <button onClick={() => { ctx.minus('green') }}>-</button><button onClick={() => { ctx.checkId('green') }}>+</button>
            </div>
            <div className={Classes.underline}></div>
          </div>) : (<div style={{ display: "none" }}></div>)
          } */}
          <div className={Classes.totalName}>
            <div ><h1>Total Amount</h1> <h1>${totalPrice}</h1></div>
            <div><button onClick={() => { ctx.clearCart() }}>Clear Cart</button>
              <button onClick={() => { ctx.onFlagOut() }}>Close</button>
              <button onClick={() => { ctx.onFlagOut() }} >Order</button></div>
          </div>
        </div>
      }
      {!ctx.isFlag && <div style={{ display: 'none' }}></div>}
    </React.Fragment >


  )
}
export default Cart;