import React from "react";
import { useContext } from "react";
import AuthContext from "../context/AuthContext";
import Classes from './MenuContainer.module.css'

const MenuCont = (props) => {
  const ctx = useContext(AuthContext);
  const addFunction = (e) => {
    e.preventDefault();
    let id = e.target.id
    ctx.checkId(id);
  }
  return (
    <React.Fragment>
      <div className={Classes.mainMenudiv}>
        <div>
          <h1>{props.title}</h1>
          <p>{props.discription}</p>
          <p className={Classes.mainMenudivp}>{props.ammount}</p>
        </div>
        <form ><div><label>Amount</label>
          <input type='number' value={parseInt(props.value)} /></div>
          <button onClick={addFunction} id={props.id} >+ Add</button>
        </form>
      </div>
      <div className={Classes.mainMenubr}></div>
    </React.Fragment>

  )
}

export default MenuCont;